export class AbstractClassAssociationSource {
    role?: string;
    cls?: string;
    isMandatory?: boolean;
    isMany?: boolean;
    isMandatoryEnforced?: boolean;

    constructor(input?: any) {
        if (input) {
            Object.assign(this, input);
        }
    }
}
